<template>
  <div class="servicedatequery">
    <el-card class="box-card">
      <div slot="header" class="clearfix">
        <el-page-header @back="goBack" content="客户电子发票查询">
        </el-page-header>
      </div>
      <h3>电子发票查询</h3>
      <div class="search">
        <div class="search_list">
          <span style="width: 50px">日期：</span>
          <el-date-picker
            v-model="date"
            type="daterange"
            unlink-panels
            range-separator="至"
            start-placeholder="开始日期"
            end-placeholder="结束日期"
            :picker-options="pickerOptions"
            size="small"
          >
          </el-date-picker>
        </div>
        <div class="doc_number">
          <span>单据号：</span>
          <el-input
            type="text"
            placeholder="请输入单据号"
            v-model="doc_number"
            size="small"
          ></el-input>
        </div>
        <div class="member">
          <span>单位名称：</span>
          <el-input
            type="text"
            placeholder="请输入单位名称"
            v-model="member"
            size="small"
          ></el-input>
        </div>
        <el-button class="el-icon-search" size="small" @click="submitResult"
          >查询</el-button
        >
      </div>
      <el-divider>查询结果</el-divider>
      <div class="searchResault" v-show="!isEmpty">
        <p>药品质量档案</p>
        <RightTable :tableData="tableData" @checkEmpty="checkEmpty" />
      </div>
      <el-empty description="描述文字" v-show="isEmpty"></el-empty>
    </el-card>
  </div>
</template>

<script>
import RightTable from "../RightNavComponents/RightTable.vue";
export default {
  name: "ServiceDataQuery",
  components: { RightTable },
  data() {
    return {
      pickerOptions: {
        shortcuts: [
          {
            text: "最近一周",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
              picker.$emit("pick", [start, end]);
            },
          },
          {
            text: "最近一个月",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
              picker.$emit("pick", [start, end]);
            },
          },
          {
            text: "最近三个月",
            onClick(picker) {
              const end = new Date();
              const start = new Date();
              start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
              picker.$emit("pick", [start, end]);
            },
          },
        ],
      },
      date: "", //日期
      member: "", //单位名称
      doc_number: "", //单据号
      isEmpty: false,
      tableData: [],
    };
  },
  watch: {
    date: {
      handler(newVal) {
        if (!newVal) {
          this.date = "";
          return;
        }
        newVal.forEach((item, index) => {
          const { y, m, d } = this.formatDate(item);
          newVal[index] = `${y}-${m}-${d}`;
        });
      },
      deep: true,
    },
  },
  methods: {
    // 格式化时间
    formatDate(t) {
      let date = new Date(t);
      var y = date.getFullYear();
      var m =
        date.getMonth() + 1 < 10
          ? "0" + (date.getMonth() + 1)
          : date.getMonth() + 1;
      var d = date.getDate() < 10 ? "0" + date.getDate() : date.getDate();
      return { y, m, d };
    },
    // 获取当前时间
    getCurrentTime() {
      let currentTime = new Date();
      const { y, m, d } = this.formatDate(currentTime);
      this.date = [];
      this.date[0] = `${y}-${m}-01`;
      this.date[1] = `${y}-${m}-${d}`;
    },
    //返回上一级
    goBack() {
      this.$router.push("/DataCenter");
    },
    // 检测数据是否为空
    checkEmpty(arr) {
      if (arr.length === 0) {
        this.isEmpty = true;
      } else {
        this.isEmpty = false;
      }
    },
    // 点击查询start_rq, end_rq, djbh, dwmch, username
    async submitResult() {
      const { data: res } = await this.$http.post("/catalogue/getData", {
        start_rq: this.date[0],
        end_rq: this.date[1],
        djbh: this.doc_number,
        dwmch: this.member,
        username: this.$store.state.dzyname,
      });
      if (res.code != 200) {
        this.tableData = [];
        this.$message.success(res.value);
        return;
      }
      this.tableData = res.data;
      this.$message.success(res.value);
    },
  },
  created() {
    this.getCurrentTime();
  },
};
</script>

<style lang="less" scoped>
.servicedatequery {
  height: 100%;
  .box-card {
    height: 100%;
    h3 {
      margin: 0 0 20px 0;
      padding: 0;
    }
    .search {
      display: flex;
      align-items: center;
      justify-content: space-around;
      margin-bottom: 40px;
      div {
        display: flex;
        span {
          text-align: center;
          width: 100px;
          font-size: 13px;
          height: 32px;
          line-height: 32px;
        }
        .el-input {
          flex: auto;
        }
      }
    }
    .searchResault {
      p {
        text-align: left;
        font-size: 14px;
      }
    }
  }
}
</style>
